import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    cityName: "北京",
    cityId: "110100",
    isPosition: true,
    index: null,
    latitude: null, //维度
    longitude: null,//经度,
    userData: null,

  },
  mutations: {
    changeCity(state, value) {
      state.cityName = value.name
      state.cityId = value.cityId
    },
    position(state, value) {
      state.isPosition = value
    },
    sw(state, value) {
      state.index = value
    },
    setXy(state, value) {
      state.latitude = value.latitude;
      state.longitude = value.longitude
    },
    user(state, value) {
      state.userData = value
    },
  },
  actions: {},
  modules: {}
})
