import Vue from 'vue'
import VueRouter from 'vue-router'

const originalPush = VueRouter.prototype.push

VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)
const routes = [
  {
    path: "/main", component: () => import("@/views/Main.vue"), redirect: "/main/movie", children: [
      {
        path: "/main/movie", component: () => import("@/views/Movie.vue"), redirect: "/main/movie/on", children: [
          {path: "/main/movie/on", component: () => import("@/components/On.vue")},
          {path: "/main/movie/off", component: () => import("@/components/Off.vue")}
        ]
      },
      {path: "/main/cinema", component: () => import("@/views/Cinema.vue"),},
      {path: "/main/mine", component: () => import("@/views/Mine.vue")}
    ]
  },

  {path: "/main/movie/on/detail/:id", component: () => import("@/views/Detail.vue")},
  {path: "/main/city", component: () => import("@/components/City.vue")},
  {
    path: "/main/cinema/:id/film", component: () => import("@/components/Film.vue"), children: [
      {name: "aaa", path: `/main/cinema/:id/film/:filmid/:time`, props: true, component: () => import("@/components/Specific.vue")}
    ]
  },
  {path: "/main/cinema/search", component: () => import("@/components/Search.vue")},
  {path: "/main/cinema/:id", component: () => import("@/components/No.vue")},
  {path: "/login", component: () => import("@/components/Login.vue")},
  {path: "/user/order", component: () => import("@/components/my/Order.vue"), meta: {isv: true}},
  {path: "/user/card", component: () => import("@/components/my/Card.vue"), meta: {isv: true}},
  {path: "/user/redPacket", component: () => import("@/components/my/RedPacket.vue"), meta: {isv: true}},
  {path: "/user/balance", component: () => import("@/components/my/Balance.vue"), meta: {isv: true}},
  {path: "/user/setting", component: () => import("@/components/my/Setting.vue"), meta: {isv: true}},
  {path: "*", redirect: "/main"}
]


const router = new VueRouter({
  mode: 'hash',
  base: "maizuo",
  routes,
  // scrollBehavior(to, from, savedPosition) {
  //   if (savedPosition) {
  //     return savedPosition
  //   } else {
  //     return {x: 0, y: 100} //期望滚动到哪个的位置
  //   }
  // },
})
router.beforeEach((to, from, next) => {
  if (to.meta.isv) {
    if (localStorage.getItem("token")) {
      next()
    } else {
      next("/login")
    }
  } else {
    next()
  }
})
export default router
